import web3 from "../contracts/initWeb3";
import { BigNumber } from 'bignumber.js'

/**
 * 18
 *  wei ,4
 *  @param {string} value 
 */
const toolFromWei = (value: string | import("bn.js")) =>{
    const valueEther =  web3.utils.fromWei(value, 'ether');
    var re = /([0-9]+\.[0-9]{2})[0-9]*/;
    const valueNumber =  valueEther.replace(re,"$1");
    return valueNumber;
}

const toolFromNumber = (value: number) =>{
    const valueNumber =  value.toFixed(2)
    return valueNumber;
}


/**
 *  2000-01-01 00:00:00
 * @param {number} timeStamp 
 */
const conversionDate = (timeStamp:number) => {
    if(timeStamp>0){
        var date = new Date(timeStamp*1000); //10*1000131000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
        var D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
        var m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
        var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
        return Y + M + `${D} ` + h + m + s; //
    }
    return '0000-00-00';
};

/**
 * 
 * @param {string} timeStamp 
 */
 const showTime = function (timeStamp:string) {
    const nowtime = new Date();  //
    const endtime =  Number(timeStamp) * 1000;  //
    const lefttime = endtime - nowtime.getTime(),  //
        leftd = Math.floor(lefttime/(1000*60*60*24)),  //
        lefth = Math.floor(lefttime/(1000*60*60)%24),  //
        leftm = Math.floor(lefttime/(1000*60)%60),  //
        lefts = Math.floor(lefttime/1000%60);  //

    return leftd + "" + lefth + ":" + leftm + ":" + lefts;  //
}

const showTimeText = function (timeStamp:string) {
    
    const nowtime = new Date();  //
    const endtime =  Number(timeStamp) * 1000;  //
    const lefttime = endtime - nowtime.getTime(),  //
        // leftd = Math.floor(lefttime/(1000*60*60*24)),  //
        lefth = Math.floor(lefttime/(1000*60*60)%24),  //
        leftm = Math.floor(lefttime/(1000*60)%60),  //
        lefts = Math.floor(lefttime/1000%60);  //
    
        if(lefttime <= 0){
            return ''
        }else {
             return lefth + ":" + leftm + ":" + lefts;  //
        }
}

/**
 *  0  1 2
 * @param {string} timeStamp 
 */
const showTimeState = function (timeStamp:string) {
    const nowtime = new Date().getTime();  //
    const endtime =  Number(timeStamp) * 1000;  //
    const remaining = endtime - nowtime;
    if(remaining > 0 && remaining < 86400000){
        return ''
    }else if(remaining <= 0){
        return ''
    }else{
        return ''
    }
}

const endAuctionTime = (timeStamp:number) =>{
    const nowtime = new Date().getTime();  //
    const endtime =  Number(timeStamp) * 1000;  //
    if(nowtime >= endtime){
        return true
    }else{
        return false
    }
}

/**
 *  2000-01-01 00:00:00
 */
function getCurrentTime() {
    var d = new Date(),
        str = '';
    str += d.getFullYear() + '-'; 
    str += d.getMonth() + 1 + '-'; 
    str += d.getDate() + ' ';
    str += d.getHours() + ':';
    str += d.getMinutes() + ':';
    str += d.getSeconds() + '';
    return str;
}



/**  */
const releasableIsMumber = (totalShares: string, totalReleased: string, releasable: string) => {
    let shares = new BigNumber(Number(totalShares))
    let difference = shares.minus(Number(totalReleased)).toNumber()
    return difference < Number(releasable) ? '0': releasable
}


export{
    toolFromWei,
    conversionDate,
    endAuctionTime,
    showTime,
    getCurrentTime,
    showTimeState,
    showTimeText,
    toolFromNumber,
    releasableIsMumber
}

